.container,
.backdrop,
.main {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.backdrop,
.main {
    position: absolute;
}

.container {
    position: fixed;
    z-index: 2;
}

.backdrop {
    opacity: 0.9;
}

.main {
    display: flex;
    align-items: center;
    justify-content: center;
}

.body {
    border-width: 1px;
    border-style: solid;
    padding: 16px;
    width: 480px;
}
