@import "@moai/core/dist/theme/module.scss";

.container {
    display: flex;
    align-items: center;
    transition: background-color 0.1s;

    @include light {
        &:hover {
            background-color: var(--light-gray-2);
        }
        &.over {
            background-color: var(--light-gray-2);
        }
    }
    @include dark {
        &:hover {
            background-color: var(--dark-gray-2);
        }
        &.over {
            background-color: var(--dark-gray-2);
        }
    }
}

.field {
    flex: 0 0 auto;
}

.field1st {
    flex: 1 1 0px;
    overflow: hidden;
    padding: 4px;
    margin: -4px;
}

.field2nd {
    flex: 0 0 auto;
    text-align: right;
    padding: 4px;
    margin: -4px;
}

.fields {
    display: flex;
}
