@import "@moai/core/dist/theme/module.scss";

.container {
    --light-gray-4: #adadad;
    --light-gray-5: #575757;
    @include light {
        color: var(--light-gray-5);
    }

    --dark-gray-4: #919292;
    --dark-gray-5: #fcfdfd;
    @include dark {
        color: var(--dark-gray-5);
    }
}
.container input[type="checkbox"] {
    width: 14px;
    height: 14px;
    margin-top: 3px;
    margin-left: 3px;
    border-radius: 3px;

    @include light {
        background: #ffffff;
        border: 0.5px solid #b7b7b7;
        border-top-color: #a5a5a5;
        box-shadow: inset 0px 0.5px 1px rgba(0, 0, 0, 0.2);
    }

    @include dark {
        background-image: linear-gradient(180deg, #616161 0%, #797979 100%);
        border: none;
        box-shadow: inset 0 0.5px 0 #7f7f7f, inset 0 1px 0 #666666,
            0 0.5px 0.5px #292929;
    }
}

.container button,
.container select,
.container input[type="text"] {
    height: 23px;
    outline: revert; // Safari and Firefox
    border-radius: 4px;
    @include light {
        background: linear-gradient(180deg, #fefefe 0%, #f1f1f1 100%);
        box-shadow: 0px 0.5px 0px #c6c6c6;
        border: solid 0.5px #c3c3c3;
        border-bottom-color: #a0a0a0;
        border-top-color: #cacaca;
    }
    @include dark {
        background: linear-gradient(180deg, #7a7a7a 0%, #757575 100%);
        box-shadow: inset 0 0.5px 0 #949494, inset 0 1px 0 #7f7f7f;
        border: solid 0.5px #4a4a4a;
    }
}

.container button:active {
    @include light {
        background: linear-gradient(180deg, #e3e3e3 0%, #d4d4d4 100%);
    }
    @include dark {
        background: linear-gradient(180deg, #8f8d8d 0%, #818080 100%);
    }
}

.borderWeak hr,
.borderStrong hr {
    @include light {
        border-top: solid 0.5px #a6a6a6;
        border-bottom: solid 0.5px #bebebe;
    }
    @include dark {
        border-top: solid 0.5px #383939;
        border-bottom: solid 0.5px #000000;
    }
}
