.container {
    padding: 8px;
    position: relative;
}

.main {
    transition: transform 0.15s ease-out;
    position: relative;
    display: flex;
}

.container.edit .main {
    transform: translateY(-8px);
    // To keep the header name position when entering edit mode. This is the
    // same amount as the margin-left at table/active/main/container
    padding-left: 28px;
    padding-right: 28px;
}

.remove {
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0;
    height: 100%;
    transition: opacity 0.15s;
}

.container:hover .remove,
.container:focus-within .remove {
    opacity: 1;
}

.title {
    flex: 1 1 0px;
}
